import React from 'react'
import { NavLink } from "react-router-dom";
import { Container } from 'react-bootstrap'

export default function BlogCard() {

    return (
        <div className="container text-center border border-dark border-4 p-5 mb-5 shadow">
            <div className='row'>
                <Container className="text-center ">
                    <h1 className="my-1 display-3 fw-bold">My Personal Blog</h1>
                    <hr className="my-4" />
                    <p className="lead">
                        <NavLink className="btn btn-success btn-lg" to="/blog">
                            Go to my blog
                        </NavLink>
                    </p>
                </Container>
            </div>
        </div>
    )
}
