import React from 'react'
import { Navbar, Nav, Container } from 'react-bootstrap'
import { HashLink } from 'react-router-hash-link'


export default function NavBar() {
    return (
        <Navbar bg="dark" expand="md" variant="dark" fixed="top" className="shadow" style={{ backgroundColor: "#071740", position: "sticky" }}>
            <Container fluid>
                <HashLink className='navbar-brand' to="/#about">Hector Magana</HashLink>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">
                        <HashLink className='nav-link' to="/#about">About</HashLink>
                        <HashLink className='nav-link' to="/#projects">Projects</HashLink>
                        <HashLink className='nav-link' to="/#games">Games</HashLink>
                        <HashLink className='nav-link' to="/#blog">Blog</HashLink>
                        <HashLink className='nav-link' to="/#contact">Contact</HashLink>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    )
}
